import {createApp} from 'vue'
import App from './App.vue'
import {createRouter, createWebHistory} from 'vue-router'
import LoginPage from './pages/PreLogin/LoginPage.vue'
import SignupPage from './pages/PreLogin/SignupPage.vue'
import ForgotPasswordPage from './pages/PreLogin/ForgotPasswordPage.vue'
import ResetPasswordPage from './pages/PreLogin/ResetPasswordPage.vue'
import VueApexCharts from "vue3-apexcharts";
import UserDashboardPage from "./pages/UserDashboard/UserDashboardPage.vue"
import ScenarioPage from './pages/ScenarioDetails/ScenarioPage.vue'
import SessionReportPage from './pages/Session/SessionReportPage.vue'
import VuePlyr from 'vue-plyr'
import 'vue-plyr/dist/vue-plyr.css'

const routes = [
    { path: '/', component: LoginPage },
    { path: '/signup', component: SignupPage },
    { path: '/dashboard', component: UserDashboardPage },
    { path: '/scenario/:id', component: ScenarioPage },
    { path: '/session/report/:id', component: SessionReportPage },

    { path: '/forgot-password', component: ForgotPasswordPage },
    { path: '/reset-password', component: ResetPasswordPage },
]

const router = createRouter({
    history: createWebHistory(),
    routes,
})

const app = createApp(App)
app.use(router)
app.use(VueApexCharts)
app.use(VuePlyr, {
  plyr: {
    controls: [
        'play', 
        'progress', 
        'current-time',
        'pip',
    ]
  }
})
app.mount('#app')
