        controls
        autoplay
        muted
        ref="player"
    >
        <Video poster="https://media.vimejs.com/poster.png">
            <source data-src="https://media.vimejs.com/720p.mp4" type="video/mp4">
        </Video>
    </Player>
</template>
<script>
    import { 
        Player,
        Video,
    } from '@vime/vue-next';
    import '@vime/core/themes/default.css';
    export default {
        name: "VideoPlayer",
        components: {
            Player,
            Video,
        }
    }
