<template>
    <div class="relative w-full h-full">
        <img class="absolute top-0 left-0 bottom-right pointer-events-none opacity-10" src="../../../assets/Vector.png" alt="">
        <img class="absolute bottom-0 right-0 bottom-right" src="../../../assets/images/echo-bottom-right.png" alt="">
        <div class="flex flex-row items-center justify-center h-full">
            <div class="flex-shrink w-2/12 3xl:w-3/12"></div>
            <div class="flex flex-col flex-1 text-left z-10">
                <div class="flex-none font-bold font-ubuntu text-3xl leading-10 tracking-orca-wide mr-20">
                    Forgot Password?
                </div>
                <div class="flex-none font-lato 3xl:text-lg text-orca-gray-5 mt-2 tracking-wide">Enter your registered email id and we’ll send you a link to reset your password </div>
                <form action="#" @submit="validateForm" novalidate="true">
                <div class="flex-none mt-10 w-full relative">
                    <input class="w-full h-12  bg-orca-black-light rounded-md p-4 placeholder-orca-gray-3 font-lato text-sm focus:outline-none focus:ring-1 focus:ring-orca-sky-blue" :type="passwordFieldType"  :class="this.passwordErrors.length ? 'border-red-error': '' "  v-model="password" placeholder="Enter your password">
                    <span class="cursor-pointer absolute top-3 right-4"  @click="switchVisibility"><i :class="[isActive ? activeClass : 'far fa-eye-slash', 'fas fa-eye']"></i></span>
                    <div class="h-4 ">
                        <p class="text-left text-xs text-red-500 lg:pt-1.5 4xl:pt-0 4xl:text-xl" v-if="passwordErrors.length">
                            <ul>
                                <li v-for="error in passwordErrors" :key="error" >{{ error }}</li>
                            </ul>
                        </p>
                    </div>
                    <input class="w-full h-12 bg-orca-black-light rounded-md p-4 placeholder-orca-gray-3 font-lato text-sm focus:outline-none focus:ring-1 focus:ring-orca-sky-blue mt-4" :type="passwordFieldType" :class="this.confirmPasswordErrors.length ? 'border-red-error': '' "  v-model="confirmPassword" placeholder="Re-enter your password">
                    <span class="cursor-pointer absolute top-11 right-4 mt-12"  @click="switchVisibility"><i :class="[isActive ? activeClass : 'far fa-eye-slash', 'fas fa-eye']"></i></span>
                    <div class="h-4 ">
                        <p class="text-left text-xs text-red-500 lg:pt-1.5 4xl:pt-0 4xl:text-xl" v-if="confirmPasswordErrors.length">
                            <ul>
                                <li v-for="error in confirmPasswordErrors" :key="error" >{{ error }}</li>
                            </ul>
                        </p>
                    </div>
                </div>
                <div class="mt-8">
                    <button class="rounded-full w-full bg-orca-sky-blue h-12 font-bold text-lg focus:outline-none"> Reset Password</button>
                </div>
                <h3 class="font-bold font-ubuntu mt-12">Password must contain</h3>
                <ul class="text-xs font-lato mt-4">
                    <li class="flex">
                        <span class="mr-2"><i :class="hasUppercase ? 'fas fa-check-circle text-orca-green-light':'fas fa-circle text-orca-gray-6'"></i></span> 
                        <span>Atleast 1 upper case letter (A-Z)</span>
                    </li>
                    <li class="flex mt-4">
                        <span class="mr-2"><i :class="hasNumber ? 'fas fa-check-circle text-orca-green-light':'fas fa-circle text-orca-gray-6'"></i></span> 
                        <span>Atleast 1 number (0-9)</span>
                    </li>
                    <li class="flex mt-4">
                        <span class="mr-2"><i :class="hasMinLength ? 'fas fa-check-circle text-orca-green-light':'fas fa-circle text-orca-gray-6'"></i></span> 
                        <span>Atleast 8 characters</span>
                    </li>  
                </ul>
                </form>
            </div>
            <div class="flex-shrink w-2/12 3xl:w-3/12"></div>
        </div>  
    </div>
</template>

<script>
export default {
    name: "ResetPasswordSection",
    data() {
        return{
            password:'',
            confirmPassword:'',  
            passwordErrors: [],
            confirmPasswordErrors:[],
            passwordFieldType: 'password',
            isActive: true,
            hasMinLength: false,
            hasNumber: false,
            hasUppercase: false
        }
    },

    watch:{ 
        password(){
            this.hasMinLength = (this.password.length >= 8);
            this.hasNumber    = /\d/.test(this.password);
            this.hasUppercase = /[A-Z]/.test(this.password);
        }
    },   

    methods: {
        switchVisibility() {
            this.passwordFieldType = this.passwordFieldType === 'password' ? 'text' : 'password'
            this.isActive = !this.isActive
        },

        validateForm(e) {
            this.passwordErrors = [];
            this.confirmPasswordErrors = [];

            if (!this.password) {
                this.passwordErrors.push('Please enter a password');
            }
         
            if (!this.confirmPassword) {
                this.confirmPasswordErrors.push('Please re-enter the password');
            }
            if(this.confirmPassword.length && this.password !== this.confirmPassword){
                this.passwordErrors.push('Passwords do not match')
                this.confirmPasswordErrors.push('Passwords do not match')
            }
            if ((!this.passwordErrors.length && !this.confirmPasswordErrors.length) && (this.hasMinLength && this.hasNumber && this.hasUppercase)) {
                return true;
            }
            e.preventDefault();
        },

    }
}
</script>

<style>
    .bottom-right {
        margin-right: -22px
    }
    .border-red-error{
        border: 0.5px solid #F25544
    }
</style>