<template>
    <div class="relative w-full h-full">
        <img class="absolute top-0 left-0 bottom-right pointer-events-none opacity-10" src="../../../assets/Vector.png" alt="">
        <img class="absolute bottom-0 right-0 bottom-right" src="../../../assets/images/echo-bottom-right.png" alt="">
        <div class="flex flex-row items-center justify-center h-full">
            <div class="flex-shrink w-2/12 3xl:w-3/12"></div>
            <div class="flex flex-col flex-1 text-left z-10">
                <div class="flex-none font-bold font-ubuntu text-3xl leading-10 tracking-orca-wide mr-20">
                    Forgot Password?
                </div>
                <div class="flex-none font-lato 3xl:text-lg text-orca-gray-5 mt-2 tracking-wide">Enter your registered email id and we’ll send you a password </div>
                <form action="#" @submit="validateForm" novalidate="true">
                    <div class="flex-none mt-10 w-full">
                    <input class="w-full h-12 bg-orca-black-light rounded-md p-4 placeholder-orca-gray-3 font-lato text-sm focus:outline-none focus:ring-1 focus:ring-orca-sky-blue" type="text" :class="this.errors.length ? 'border-red-error': '' "  v-model="email" placeholder="Enter your email id">
                    <div class="h-4 ">
                        <p class="text-left text-xs text-red-500 lg:pt-1.5 4xl:pt-0 4xl:text-xl" v-if="errors.length">
                            <ul>
                                <li v-for="error in errors" :key="error" >{{ error }}</li>
                            </ul>
                        </p>
                    </div>
                    <div class="text-right">
                        <router-link to="/reset-password" class="flex-none font-lato text-xs  mt-3 border p-2 rounded-full">Or create your own password ?</router-link>
                    </div>
                </div>
                <div class="mt-24">
                    <button class="rounded-full w-full bg-orca-sky-blue h-12 font-bold text-lg focus:outline-none"> Submit</button>
                </div>
                </form>
            </div>
            <div class="flex-shrink w-2/12 3xl:w-3/12"></div>
        </div>  
    </div>
</template>

<script>
export default {
    name: "ForogotPasswordSection",
    data(){
        return{
            email:'',  
            isActive: true,
            errors: [],
        }
    },
    methods: {
        validateForm(e) {
            this.errors = [];
            if (!this.email) {
                this.errors.push('Please enter your email');
            } 

            else if (!this.isValidEmail(this.email)) {
                this.errors.push('Please enter correct email id');
            }

            if (!this.errors.length) {
                return true;
            }
            e.preventDefault();
        },

        isValidEmail(email) {
            var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            return re.test(email);
        }
    }
}
</script>

<style>
    .bottom-right {
        margin-right: -22px
    }
    .border-red-error{
        border: 0.5px solid #F25544
    }
</style>