<template>
    <div class="relative w-full h-full">
        <img class="absolute top-0 left-0 bottom-right pointer-events-none opacity-10" src="../../../assets/Vector.png" alt="">
        <img class="absolute bottom-0 right-0 bottom-right" src="../../../assets/images/echo-bottom-right.png" alt="">
        <div class="flex flex-row items-center justify-center h-full">
            <div class="flex-shrink w-2/12 3xl:w-3/12"></div>
            <div class="flex flex-col flex-1 text-left z-10">
                <div class="flex-none font-bold font-ubuntu text-3xl leading-10 tracking-orca-wide">
                    Welcome! Let’s get started
                </div>
                <div class="flex-none font-lato 3xl:text-lg text-orca-gray-5 mt-2 tracking-wide">Lorem ipsum dolor sit amet, consectetur adipiscing sollicitudin eget.</div>
                <form action="#" class="" @submit="validateForm" novalidate="true">
                    <div class="flex-none mt-10">
                        <input class="w-full h-12 bg-orca-black-light rounded-md p-4 placeholder-orca-gray-3 font-lato text-sm focus:outline-none focus:ring-1 focus:ring-orca-sky-blue" type="text" v-model="name" :class="this.nameErrors.length ? 'border-red-error': '' " placeholder="Enter your full name">
                        <div class="h-4 mt-2">
                            <p class="text-xs text-red-500" v-if="nameErrors.length">
                                <ul>
                                    <li v-for="error in nameErrors" :key="error" >{{ error }}</li>
                                </ul>    
                            </p>    
                        </div>                    
                    </div>
                    <div class="flex-none w-full mt-2">
                        <input class="w-full h-12 bg-orca-black-light rounded-md p-4 placeholder-orca-gray-3 font-lato text-sm focus:outline-none focus:ring-1 focus:ring-orca-sky-blue" type="email" v-model="email" :class="this.errors.length ? 'border-red-error': '' "   placeholder="Enter your email id">
                        <div class="h-4 mt-2">
                            <p class="text-xs text-red-500" v-if="errors.length">
                                <ul>
                                    <li v-for="error in errors" :key="error" >{{ error }}</li>
                                </ul>
                            </p>    
                        </div>    
                    </div>
                    <div class="flex-none mt-2 w-full relative">
                        <input class="w-full h-12 bg-orca-black-light rounded-md p-4 placeholder-orca-gray-3 font-lato text-sm focus:outline-none focus:ring-1 focus:ring-orca-sky-blue" :type="passwordFieldType" v-model="password" :class="this.passwordErrors.length ? 'border-red-error': '' " placeholder="Enter your password ">
                        <span class="cursor-pointer absolute right-4 top-3 4xl:top-6"  @click="switchVisibility"><i :class="[isActive ? activeClass : 'far fa-eye-slash', 'fas fa-eye']"></i></span>
                    </div>
                    <div class="flex justify-between">
                        <div class="h-4 mt-3">
                            <p class="text-xs text-red-500" v-if="passwordErrors.length">
                                <ul>
                                    <li v-for="error in passwordErrors" :key="error" >{{ error }}</li>
                                </ul>
                            </p>
                        </div>
                        <router-link to="/forgot-password" class="flex-none font-lato text-xs text-right mt-3">Forgot Password ?</router-link>
                    </div>
                    <div class="mt-24">
                        <button class="rounded-full w-full bg-orca-sky-blue h-12 font-bold text-lg focus:outline-none"> Signup</button>
                    </div>
                </form>
                <div class="mt-10 text-sm text-orca-gray-3">
                    or continue with the social profile for login
                </div>
                <div class="flex flex-row mt-3 items-center">
                    <img class="flex-none mr-2" src="../../../assets/LinkedIn-icon.png" alt="">
                    <span class="flex-none mr-4">
                        LinkedIn
                    </span>
                    <img class="flex-none mr-2" src="../../../assets/Google-icon.png" alt="">
                    <span class="flex-none">
                        Google
                    </span>
                </div>
                <div class="flex mt-10 text-sm">
                    <p class="text-orca-gray-3">Already have an account?</p>
                    <router-link to='/' class="ml-2 font-medium underline">Login</router-link>
                </div>
            </div>
            <div class="flex-shrink w-2/12 3xl:w-3/12"></div>
        </div>  
    </div>
</template>
<script>
export default {
    name: "SignupSection",

    data(){
        return{
            name:'',
            email:'',  
            password: '',
            passwordFieldType: 'password',
            isActive: true,
            errors: [],
            passwordErrors: [],
            nameErrors:[]
        }
    },

    methods: {
        switchVisibility() {
            this.passwordFieldType = this.passwordFieldType === 'password' ? 'text' : 'password'
            this.isActive = !this.isActive
        },

        validateForm(e) {
            this.errors = [];
            this.passwordErrors = []
            this.nameErrors = []
            
            if(!this.name){
                this.nameErrors.push('Please enter your name')
            }

            if (!this.email) {
                this.errors.push('Please enter your email');
            } else if (!this.isValidEmail(this.email)) {
                this.errors.push('Please enter correct email id');
            }

            if(!this.password){
                this.passwordErrors.push('Please enter your password')
            }

            if (!this.errors.length) {
                return true;
            }

            if (!this.passwordErrors.length) {
                return true;
            }

            e.preventDefault();
        },

        isValidEmail(email) {
            var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            return re.test(email);
        },
    }
}
</script>

<style>
    .bottom-right {
        margin-right: -22px
    }
    .border-red-error{
        border: 0.5px solid #F25544
    }
</style>