// Code taken and modified from https://gist.github.com/thomlom/4d2149161ec8c6d3fdfe530baf328c40
<template>
    <label class="container w-20 h-20">
        <input
            v-bind="$attrs"
            class="input"
            type="checkbox"
            :checked="checked"
            @change="$emit('update:checked', $event.target.checked)"
        />
        <span class="switch"></span>
        <span class="label">{{ label }}</span>
    </label>
</template>

<script>
    export default {
    name: "Switch",
    props: {
        label: {
            type: String,
            required: false,
        },
        checked: {
            type: Boolean,
            required: true,
        },
    },
    };
</script>

<style scoped>
    .container {
        cursor: pointer;
        display: flex;
        align-items: center;
    }

    .label {
        margin-left: 12px;
        color: #1a202c;
        /* Show an ellipsis if the text takes more than one line */
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    /* Visually hide the checkbox input */
    .input {
        position: absolute;
        width: 1px;
        height: 1px;
        padding: 0;
        margin: -1px;
        overflow: hidden;
        clip: rect(0, 0, 0, 0);
        white-space: nowrap;
        border-width: 0;
    }

    .switch {
        --switch-container-width: 40px;
        --switch-size: calc(var(--switch-container-width) / 2);

        --light-gray: #e2e8f0;
        --gray: #cbd5e0;
        --dark-gray: #a0aec0;
        --blue: #00adca;
        --dark-blue: #319795;

        /* Vertically center the inner circle */
        display: flex;
        align-items: center;
        position: relative;
        height: var(--switch-size);
        flex-basis: var(--switch-container-width);
        /* Make the container element rounded */
        border-radius: var(--switch-size);
        background-color: var(--light-gray);
        /* In case the label gets really long, the toggle shouldn't shrink. */
        flex-shrink: 0;

        transition: background-color 0.25s ease-in-out;
    }

    .switch::before {
        content: "";
        position: absolute;
        /* Move a little bit the inner circle to the right */
        left: 1px;
        height: calc(var(--switch-size) + 10px);
        width: calc(var(--switch-size) + 10px);
        /* Make the inner circle fully rounded */
        border-radius: 9999px;
        background-color: white;
        border: 2px solid var(--light-gray);
        margin-left: -10px;

        transition: transform 0.375s ease-in-out;
    }

    .input:checked + .switch {
        background-color: var(--blue);
    }

    .input:checked + .switch::before {
        border-color: var(--blue);
        /* Move the inner circle to the right */
        transform: translateX(
            calc(var(--switch-container-width) - var(--switch-size) + 10px)
        );
    }

    .input:focus + .switch::before {
        border-color: var(--gray);
    }

    .input:focus:checked + .switch::before {
        border-color: var(--dark-blue);
    }

    .input:disabled + .switch {
        background-color: var(--gray);
    }

    .input:disabled + .switch::before {
        background-color: var(--dark-gray);
        border-color: var(--dark-gray);
    }
</style>